import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams
} from "react-router-dom";

function Parameter() {
  let { route } = useParams();
  return (
    <h3>
      {route}
    </h3>
  );
}

function Home() {
  return (
    <h1>
      Home
    </h1>
  )
}

function App() {
  return (
    <Router>
      <Route>
        <div className="App">
          <header className="App-header">
            Recipe Box
          </header>
          <Switch>
            <Route path="/app/:route">
              <Parameter />
            </Route>
            <Route path="/app">
              <Home />
            </Route>
          </Switch>
        </div>
      </Route>
    </Router>
  );
}

export default App;
